<template>
  <div style="position: relative;height: 100%">
    <div class="li">
      <ul>
        <li>标题</li>
        <li>行业</li>
        <li>类型</li>
        <li>状态</li>
        <li>发布时间</li>
        <li>操作列</li>
      </ul>
      <ul class="li-content">
        <li>人才需求的标题</li>
        <li>化工 / 新材料</li>
        <li>技术合作</li>
        <li>待审核</li>
        <li>20180829</li>
        <li><span>详情</span><span>修改</span><span @click="isClouse = true">结束</span></li>
      </ul>
      <ul class="li-content">
        <li>人才需求的标题</li>
        <li>化工 / 新材料</li>
        <li>人才聘用</li>
        <li>通过</li>
        <li>20180829</li>
        <li><span>详情</span><span>修改</span><span @click="isClouse = true">结束</span></li>
      </ul>
      <ul class="li-content">
        <li>人才需求的标题</li>
        <li>化工 / 新材料</li>
        <li>其他</li>
        <li>驳回</li>
        <li>20180829</li>
        <li><span>详情</span><span>修改</span><span @click="isClouse = true">结束</span></li>
      </ul>
      <ul class="li-content">
        <li>人才需求的标题</li>
        <li>化工 / 新材料</li>
        <li>技术合作</li>
        <li>待审核</li>
        <li>20180829</li>
        <li><span>详情</span><span>修改</span><span @click="isClouse = true">结束</span></li>
      </ul>

    </div>
    <div class="clouse" v-if="isClouse">
      <span>结束原因</span>
      <div class="clouse-select">
        <el-select v-model="value" placeholder="请选择">
          <el-option
            v-for="item in options"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>
      </div>
      <div class="clouse-btn">
        <el-button type="primary" size="small" @click="cancle">取消</el-button>
        <el-button size="small" @click="toClouse">确定</el-button>
      </div>

    </div>
    <div class="foot">
      <el-pagination
        background
        layout="prev, pager, next"
        :total="100">
      </el-pagination>
    </div>
  </div>
</template>

<script>
    export default {
        name: "TalentDemand",
        data(){
            return {
                options: [{
                    value: '选项1',
                    label: '待业调成'
                }, {
                    value: '选项2',
                    label: '供应资源不足'
                }, {
                    value: '选项3',
                    label: '设备老旧'
                }, {
                    value: '选项4',
                    label: '其他'
                }, {
                    value: '选项5',
                    label: '信息填写错误'
                }],
                value: '',
                isClouse:false
            }
        },
        methods:{
            cancle(){
                this.isClouse = false;
            },
            toClouse(){
                this.isClouse = false;
            }
        }
    }
</script>

<style scoped lang="less">
  .li {
    height: 45px;
    width: 100%;
    background-color: rgba(3, 87, 255, 0.0980392156862745);
    ul {
      display: flex;
      li {
        height: 100%;
        line-height: 45px;
        list-style: none;
        box-sizing: border-box;
        padding-left: 20px;
        font-size: 14px;
        color: #333;

      }
      li:nth-child(1){
        flex: 2;
      }
      li:nth-child(2){
        flex: 2;
      }
      li:nth-child(3){
        flex: 1;
      }
      li:nth-child(4){
        flex: 1;
      }
      li:nth-child(5){
        flex: 1;
      }
      li:nth-child(6){
        flex: 1.5;
        display: flex;
        span {
          display: inline-block;
          width: 30px;
          flex: 1;
          cursor: pointer;
        }
        span:nth-child(1){
          color: rgba(3, 87, 255, 0.749019607843137);
        }
        span:nth-child(2){
          color: rgba(3, 87, 255, 0.749019607843137);
        }
        span:nth-child(3){
          color: rgba(217, 0, 27, 0.749019607843137);
        }
      }
    }
  }
  .li-content {
    height: 72px!important;
  }
  .li-content ul li {
    line-height: 72px!important;
  }
  .clouse {
    width: 300px;
    height: 142px;
    position: absolute;
    left: 50%;
    top: 50%;
    background-color: #fff;
    border: 1px solid #333333;
    z-index: 999;
    transform: translate(-50%,-50%);
    box-sizing: border-box;
    padding: 32px 37px 15px 37px;
  }
  .clouse-select {
    display: inline-block;
    width: 138px;
    margin-left: 10px;
    margin-bottom: 10px;
  }
  .clouse-btn {
    text-align: center;
    padding-top: 15px;
  }
  .foot {
    position: absolute;
    right: 24px;
    bottom: 28px;
  }
</style>



