<template>
    <div>
      <el-dialog
        title="修改密码"
        :visible.sync="dialogVisible"
        width="576px "
        :before-close="handleClose2">
        <el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
          <el-form-item label="原密码" prop="pass">
            <el-input type="password" v-model="ruleForm.password" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="新密码" prop="checkPass">
            <el-input type="password" v-model="ruleForm.newPassword" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="确认新密码" prop="checkPass">
            <el-input type="password" v-model="ruleForm.renewPassword" autocomplete="off"></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
    <el-button @click="dialogVisible = false">取 消</el-button>
    <el-button type="primary" @click="modify">确 定</el-button>
  </span>
      </el-dialog>
      <el-dialog
        title="企业认证详情"
        :visible.sync="dialogVisible1"
        width="700px"
        :before-close="handleClose1">
        <div style="padding: 30px 0 30px 30px">
          <BaseForm :childMsg="childMsg"></BaseForm>
          <CompanyForm :childMsg1="childMsg1"></CompanyForm>
        </div>

      </el-dialog>
      <div class="accountInfo"  v-if="authentication">
           <div><span class="accountInfoT1">*</span> <span class="accountInfoT2">账号信息</span> </div>
           <div style="margin-top: 4px;position: relative">
             <span class="accountInfoB1">用户名：{{username}}</span>
             <span class="accountInfoB2">邮箱：{{email}}</span>
             <span class="accountInfoB3" style="cursor: pointer" @click="dialogVisible = true">修改密码</span>
             <span class="accountInfoB4">开通VIP 更多内容服务</span>
             <span class="accountInfoB5" style="cursor: pointer">开通</span>
           </div>
      </div>
      <div class="authentication" v-if="authentication">
        <span>企业认证</span>
        <div style="float: right">
          <span v-if="status==1" style="color: #3fc71b;font-size: 14px;box-sizing:border-box;margin-right: 15px">已完成</span>
          <span v-if="status==1" style="color: rgb(30, 88, 163);font-size: 14px;box-sizing:border-box;margin-right: 15px;cursor: pointer" @click="dialogVisible1 = true;childMsg=true;childMsg1=true">详情</span>
          <span v-if="status==2" style="color: #d40606;font-size: 14px;box-sizing:border-box;margin-right: 15px">驳回</span>
          <span v-if="status==0" style="color: #008fff;font-size: 14px;box-sizing:border-box;margin-right: 15px">待审核</span>
          <el-button type="primary" size="small" @click="toAuthentication(1)" v-if="status==2">修改</el-button>
          <el-button type="info" plain disabled v-if="status==0">修改</el-button>
          <el-button type="primary" size="small" @click="toAuthentication(3)" v-if="status=='null'">立即认证</el-button>
        </div>
      </div>

      <div class="verify" v-else>
         <div class="authentication-head">
           <div :class="current=='BaseForm'?'active':''" @click="totab(1)">1.基本信息</div>
           <!-- @click="totab(2)" -->
           <div :class="current=='CompanyForm'?'active':''" @click="totab(2)" >2.企业认证</div>
         </div>

        <div class="form">
          <keep-alive>
            <component  v-bind:flag="flagInfo"  :is="current" @baseFormUp="getBaseForm" :baseFormInfoData="baseFormInfo"></component>
          </keep-alive>
        </div>

      </div>

    </div>
</template>

<script>
  import BaseForm from "./BaseForm";
  import CompanyForm from "./CompanyForm";
    export default {
        name: "EnterpriseCertification",
        data(){
            var checkAge = (rule, value, callback) => {
                // if (!value) {
                //     return callback(new Error('年龄不能为空'));
                // }
                // setTimeout(() => {
                //     if (!Number.isInteger(value)) {
                //         callback(new Error('请输入数字值'));
                //     } else {
                //         if (value < 18) {
                //             callback(new Error('必须年满18岁'));
                //         } else {
                //             callback();
                //         }
                //     }
                // }, 1000);
            };
            var validatePass = (rule, value, callback) => {
                // if (value === '') {
                //     callback(new Error('请输入密码'));
                // } else {
                //     if (this.ruleForm.checkPass !== '') {
                //         this.$refs.ruleForm.validateField('checkPass');
                //     }
                //     callback();
                // }
            };
            var validatePass2 = (rule, value, callback) => {
                // if (value === '') {
                //     callback(new Error('请再次输入密码'));
                // } else if (value !== this.ruleForm.pass) {
                //     callback(new Error('两次输入密码不一致!'));
                // } else {
                //     callback();
                // }
            };
            return {
                childMsg:false,
                childMsg1:false,
                dialogVisible1: false,
                ruleForm: {
                    userName: '',
                    password: '',
                    newPassword:'',
                    renewPassword:'',

                },
                rules: {
                    pass: [
                        { validator: validatePass, trigger: 'blur' }
                    ],
                    checkPass: [
                        { validator: validatePass2, trigger: 'blur' }
                    ],
                    age: [
                        { validator: checkAge, trigger: 'blur' }
                    ]
                },
                current:'BaseForm',
                baseFormInfo:{},
                authentication:true,  //企业认证
                status:'',
                id:'',
                flagInfo:false,
                username:'',
                email:'',
                dialogVisible: false
            }
        },
        created(){
            //用户名
            var username = sessionStorage.getItem('username');
            var email = sessionStorage.getItem('email');
           this.status  = sessionStorage.getItem('status')
            this.email = email;
            this.username = username;
            this.getVerifyInfo()
        },
        components:{
            BaseForm,
            CompanyForm
        },

        computed:{

        },
        watch:{

        },
        methods:{
            handleClose2(done) {
                var that = this;
                that.dialogVisible = false;
                done();
            },
            handleClose1(done) {
                var that = this;
                        that.childMsg = false;
                        that.childMsg1 = false;
                        done();
            },
            modify(){
               var that = this;
               if (this.ruleForm.renewPassword != this.ruleForm.newPassword){
                   this.$message({
                       message: '两次密码输入不一致请重新输入',
                       type: 'warning'
                   });
                   this.ruleForm.renewPassword = '';
                   return false
               }
               var username = this.$cookies.get('username');
               that.$axios.post('/user/updataPassword',this.$qs.stringify({
                   userName:username,
                   password:that.ruleForm.password,
                   newPassword:that.ruleForm.newPassword
               }))
                   .then(res=>{
                       console.log(res)
                       if (res.data.code==0){
                           that.$message({
                               message: '恭喜！修改成功',
                               type: 'success'
                           });
                          that.dialogVisible = false;
                       }else {
                               this.$message.error(res.data.msg);
                       }
                   })
                   .catch(res=>{
                       this.$message.error('网络错误');
                   })
            },
            submitForm(formName) {
                // this.$refs[formName].validate((valid) => {
                //     if (valid) {
                //         alert('submit!');
                //     } else {
                //         console.log('error submit!!');
                //         return false;
                //     }
                // });
            },
            resetForm(formName) {
                // this.$refs[formName].resetFields();
            },
            handleClose(done) {
                // this.$confirm('确认关闭？')
                //     .then(_ => {
                //         done();
                //     })
                //     .catch(_ => {});
            },
            //读取认证信息
            getVerifyInfo(){
                var that = this;
                var userName = sessionStorage.getItem('username');
                this.$axios.get('bussinessCenter/findMyBusinessCenter',{
                    params:{
                       userName:userName
                    }
                }).then(res=>{
                    console.log(res)
                    if (res.data.code==0){
                        if (!res.data.data){
                            that.status = 'null';
                            sessionStorage.setItem('status','');

                            that.id = '';


                        }else {
                            console.log( !res.data.data)
                            that.status = res.data.data.statue;
                            sessionStorage.setItem('status',res.data.data.statue);
                            that.id = res.data.data.id;
                            that.$store.state.companyId = res.data.data.id

                        }
                    }else{

                    }
                    that.$forceUpdate();
                    console.log(res)
                })
            },
            //获取企业认证信息
            getCompanyInfo(){

            },
            //修改status
            modifyStatus(){
                this.status = 1;
                console.log('调用了父组件',this.status)
            },
            totab(i){
                if (i==1){
                  this.current = 'BaseForm';

                }
                if (i==2){
                    this.current = 'CompanyForm';
                }
            },
            toAuthentication(i){
                if (i==1){
                    this.authentication = false;
                    this.$store.state.companyFlag = true;
                    this.getCompanyInfo();
                    this.flagInfo = true;

                }
                if (i==2){
                    this.authentication = true;
                }
                if (i==3){
                    this.authentication = false;
                }

            },
            getBaseForm(data){
              console.log("data");
              this.baseFormInfo=data;
              console.log(data);
            }
        }
    }
</script>

<style scoped lang="less">
.authentication {
  width: 870px;
  height: 102px;
  line-height: 102px;
  box-sizing: border-box;
  padding-left: 40px;
  padding-right: 68px;
  margin: 18px;
  background-color: #ffffff;
  border: solid 1px #e7e7e7;
}
.accountInfo {
  width: 870px;
  height: 102px;
  box-sizing: border-box;
  padding: 22px 0px 0px 47px;
  margin: 18px;
  background-color: #ffffff;
  border: solid 1px #e7e7e7;
}
.verify {
  .authentication-head {
    height: 50px;
    text-align: center;
     div {
       display: inline-block;
       width: 120px;
       height: 50px;
       margin: 0 auto;
       font-size: 18px;
       font-weight: 650;
       cursor: pointer;
     }
    div:nth-child(1){

      color: #000;
      line-height: 50px;
      text-align: center;
      margin-right: 21.5px;
    }
    div:nth-child(2){
      color: #000;
      line-height: 50px;
      text-align: center;
      margin-left: 21.5px;
    }
  }
}
  .active {
    background-color: #4281ff;
    color: #FFFFFF!important;
  }
  .el-form-item__content{
    margin-left: 150px!important;
  }
  .form {
    padding: 21px 48px;
  }
  .accountInfo {

  }
  .accountInfoT1 {
    color: #C01818;
    font-size: 16px;

  }
.accountInfoT2 {
  font-size: 16px;
  color: #4E4E4E;
}
  .accountInfoB1 {
    color: #4E4E4E;
    font-size: 14px;
    font-weight:400;
    margin-right: 36px;
  }
.accountInfoB2 {
  color: #4E4E4E;
  font-size: 14px;
  font-weight:400;
  box-sizing: border-box;
  display: inline-block;
  margin-right: 46px;
}
.accountInfoB3 {
  color: #008FFF;
  font-weight:400;
  font-size:14px;
  display: inline-block;
  margin-right: 112px;
}
.accountInfoB4 {
  color: #DA9A01;
  font-size:14px;
  font-family:PingFang SC;
  font-weight:400;
  display: inline-block;
  margin-right: 16px;
}
.accountInfoB5 {
  width: 80px;
  height: 32px;
  display: inline-block;
  background:rgba(251,247,232,1);
  border:1px solid rgba(217,180,92,1);
  border-radius:4px;
  position: absolute;
  line-height: 32px;
  text-align: center;
  color: #D9B45C;
  font-size:14px;
  font-family:PingFang SC;
  font-weight:600;
  right: 40px;
  top: -7px;
}
</style>



