<template>
  <div style="position: relative;height: 100%">
    <div class="li">
      <ul>
        <li>行业领域</li>
        <li>需求概述</li>
        <li>需求类别</li>
        <li>预投金额</li>
        <li>截止时间</li>
        <li>审核状态</li>
        <li>操作列</li>
      </ul>
      <ul class="li-content" v-for="(item,key) in info" :key="key">
        <li>{{item.demandIndustry}}</li>
        <li>{{item.demandOutline}}</li>
        <li>{{item.demandType}}</li>
        <li>{{item.preInvestmentAmount}}万</li>
        <li>{{item.endDate.substring(0, 10)}}</li>
        <li v-if="item.approvalStatus==0">待审核</li>
        <li v-if="item.approvalStatus==1">已审核</li>
        <li v-if="item.approvalStatus==2">驳回</li>
        <li><span @click="getDetail(item.id,false)">详情</span><span   @click="getDetail(item.id,true)" v-if="item.approvalStatus==2">修改</span><span    v-if="item.approvalStatus!=2" style="color: #ccc;cursor: default">修改</span>

          <span v-if="item.status==3"  style="color: #ccc;cursor: default">结束</span>
          <span v-else @click="goClouse(item.id,key)">结束</span>
        </li>
      </ul>
    </div>
    <div class="clouse" v-if="isClouse">
      <span>结束原因</span>
      <div class="clouse-select">
        <el-select v-model="value" placeholder="请选择">
          <el-option
            v-for="item in options"
            :key="item.value"

            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>
      </div>
      <div class="clouse-btn">
        <el-button type="primary" size="small" @click="cancle">取消</el-button>
        <el-button size="small" @click="toClouse">确定</el-button>
      </div>

    </div>
    <div class="foot">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        background
        :page-size="8"
        :current-page.sync="currentPage"
        layout="prev, pager, next"
        :total="total">
      </el-pagination>
    </div>
    <!-- 弹框 -->
    <el-dialog title="企业需求详情" :visible.sync="dialogFormVisible">
      <div width="100%">
        <NeedRelease :formDetailData="formData" @childCloseDia="parentCloseDia"/>
      </div>
    </el-dialog>
  </div>
</template>

<script>
    import { getDemandsByID } from "@/request/urlConfig";
    import NeedRelease from "../NeedRelease"
    export default {
        name: "EnterpriseNeeds",
        components: {
          NeedRelease,
        },
        data(){
            return {
                currentPage:1,
                formData:{},
                dialogFormVisible:false,
                options: [{
                    value: '需求对接解决',
                    label: '需求对接解决'
                }, {
                    value: '企业内部解决',
                    label: '企业内部解决'
                }, {
                    value: '其余资源解决',
                    label: '其余资源解决'
                }, {
                    value: '填写错误',
                    label: '填写错误'
                }],
                value: '',
                isClouse:false,
                info:'',
                total:1,
                falg:true,
                val:'',
                i:1,
                id:'',
                key:''
            }
        },
        created(){
            this.getInfo();
        },
        methods:{
            handleSizeChange(val) {
                console.log(`每页 ${val} 条`);
            },
            handleCurrentChange(val) {
                console.log(`当前页: ${val}`);
                if (this.falg == true){
                    this.getInfo(val);
                }else {
                    this.search(this.val,val)
                }

            },
            goClouse(id,key){
                this.id = id;
                console.log(this.id);
                this.isClouse = true;
                this.key = key;
            },
            cancle(){
                this.isClouse = false;
            },
            toClouse(){
                console.log(this.value)
                var value = this.value;
                var id = this.id;
                var that = this;
                var key = Number(this.key);
                if (value==''){
                    that.$message({
                        showClose: true,
                        type: 'warning',
                        message: '请选择结束原因'
                    });
                }
                that.$axios.post('/demands/closeDemandsById',this.$qs.stringify({
                    id:id,
                    closeReason:value
                })).then(res=>{
                    console.log(res)
                    if (res.data.code==0){
                        that.$message({
                            showClose: true,
                            type: 'success',
                            message: res.data.msg
                        });
                        that.isClouse =false;
                        that.getInfo();
                    }
                })
            },
            search(val,i){
                console.log(val)
                this.val = val;
                this.falg = false;
                var pageNum = i || 1;
                var that = this;
                that.$axios.get('/demands/showByPageForCenter',{
                    params:{
                        pageNum:pageNum,
                        pageSize:8,
                        keyWord:val,
                        userName:'admin'
                    }
                }).then((res)=>{
                    console.log(res)
                    if (res.data.code == 0){
                        that.info = res.data.data.list;
                        console.log(res.data.data.list);
                        that.total = res.data.data.total;
                    }
                })
            },
            //获取企业需求信息
            getInfo(i){
                var pageNum = i || 1;
                var that = this;
                that.$axios.get('/demands/showByPageForCenter',{
                    params:{
                        pageNum:pageNum,
                        pageSize:8,
                    }
                }).then((res)=>{
                    console.log(res)
                    if (res.data.code == 0){
                        that.info = res.data.data.list;
                        console.log(res.data.data.list);
                        that.total = res.data.data.total;
                    }
                })
            },
            //获取详情
            getDetail(id,bool){
              let params={
                id
              }
              getDemandsByID(params).then(res=>{
                console.log(res);
                if(res.data.code==0){
                  this.formData=res.data.data;
                  this.formData.isEdit=bool;//区分是编辑还是查看
                  this.dialogFormVisible=true;
                }else{
                  this.dialogFormVisible=false;
                }
              })
            },
            //关闭弹框
            parentCloseDia(){
              this.dialogFormVisible=false;
              this.getInfo();

            }
        }
    }
</script>

<style scoped lang="less">
  .li {
    height: 45px;
    width: 100%;
    background-color: rgba(3, 87, 255, 0.0980392156862745);
    ul {
      display: flex;
      li {
        height: 100%;
        line-height: 45px;
        list-style: none;
        box-sizing: border-box;
        padding-left: 20px;
        font-size: 14px;
        color: #333;

      }
      li:nth-child(1){
        flex: 1;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
      }
      li:nth-child(2){
        flex: 3;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
      }
      li:nth-child(3){
        flex: 1;
      }
      li:nth-child(4){
        flex: 1;
      }
      li:nth-child(5){
        flex: 1;
      }
      li:nth-child(6){
        flex: 1;
      }
      li:nth-child(7){
        flex: 1.5;
        display: flex;
        span {
          display: inline-block;
          width: 30px;
          flex: 1;
          cursor: pointer;
        }
        span:nth-child(1){
          color: rgba(3, 87, 255, 0.749019607843137);
        }
        span:nth-child(2){
          color: rgba(3, 87, 255, 0.749019607843137);
        }
        span:nth-child(3){
          color: rgba(217, 0, 27, 0.749019607843137);
        }
      }
    }
  }
  .li-content {
    height: 72px!important;
  }
  .li-content ul li {
    line-height: 72px!important;
  }
  .clouse {
    width: 300px;
    height: 142px;
    position: absolute;
    left: 50%;
    top: 25%;
    background-color: #fff;
    border: 1px solid #333333;
    z-index: 999;
    transform: translate(-50%,-50%);
    box-sizing: border-box;
    padding: 32px 37px 15px 37px;
  }
  .clouse-select {
    display: inline-block;
    width: 138px;
    margin-left: 10px;
    margin-bottom: 10px;
  }
  .clouse-btn {
    text-align: center;
    padding-top: 15px;
  }
  .foot {
    position: absolute;
    right: 24px;
    bottom: 28px;
  }
</style>



