<template>
  <div style="position: relative;height: 100%">
    <div class="li">
      <ul>
        <li>科技成果</li>
        <li>所属行业</li>
        <li>成果优势</li>
        <li>成果阶段</li>
        <li>审核状态</li>
        <li>操作列</li>
      </ul>
      <ul class="li-content" v-for="(item,key) in info" :key="key">
        <li>{{item.resultTechnolo}}</li>
        <li>{{item.industry}}</li>
        <li>{{item.advantages}}</li>
        <li>{{item.stage}}</li>
        <li v-if="item.statue==0">待审核</li>
        <li v-if="item.statue==1">已审核</li>
        <li v-if="item.statue==2">驳回</li>
        <li v-if="item.statue==null">待审核</li>
        <li><span @click="dialogVisible=true;sid=item.id">详情</span>
          <span v-if="item.statue==2" @click="dialogVisible1 = true;sid=item.id">修改</span>
          <span v-else style="color: #ccc">修改</span>
          <span style="cursor: default;color: #ccc" v-if="item.kstatue==3">结束</span>
          <span  v-else @click="goClouse(item.id)">结束</span>
        </li>
      </ul>
    </div>
    <div class="clouse" v-if="isClouse">
      <span>结束原因</span>
      <div class="clouse-select">
        <el-select v-model="value" placeholder="请选择">
          <el-option
            v-for="item in options"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>
      </div>
      <div class="clouse-btn">
        <el-button type="primary" size="small" @click="cancle">取消</el-button>
        <el-button size="small" @click="toClouse">确定</el-button>
      </div>

    </div>
    <div class="foot">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        background
        :page-size="8"
        layout="prev, pager, next"
        :total="total">
      </el-pagination>
    </div>

    <el-dialog
      :visible.sync="dialogVisible"
      @opened="initForm"
      width="882px"
      :before-close="handleClose">
      <ScienceEntrance ref="ScienceEntrance" :childMsg="bool" :childId="sid"></ScienceEntrance>


    </el-dialog>
    <el-dialog
      :visible.sync="dialogVisible1"
      @opened="initForm1"
      width="882px"
      :before-close="handleClose1">
      <ScienceEntrance ref="ScienceEntrance1" @closedialogVisible1 = 'closedialogVisible1' @getInfo1 = 'getInfo' :childMsg2="dialogVisible1"  :childId1="sid"></ScienceEntrance>


    </el-dialog>
  </div>
</template>

<script>
  import ScienceEntrance from '../InfoManage/ScienceEntrance'
    export default {
        name: "ScientificAchievements",
        data(){
            return {
                dialogVisible1:false,
                sid:'',
                bool:true,
                dialogVisible: false,
                options: [{
                    value: '待业调成',
                    label: '待业调成'
                }, {
                    value: '供应资源不足',
                    label: '供应资源不足'
                }, {
                    value: '设备老旧',
                    label: '设备老旧'
                },  {
                    value: '信息填写错误',
                    label: '信息填写错误'
                },{
                    value: '其他',
                    label: '其他'
                },],
                value: '',
                isClouse:false,
                info:'',
                total:0,
                falg:true,
                key:'',
            }
        },
        created(){
            this.getInfo();
        },
        components:{
            ScienceEntrance
        },
        methods:{
            closedialogVisible1(){
                this.dialogVisible1 = false;
            },
            //获取详情数据
            initForm(){

                this.$refs.ScienceEntrance.initForm(this.sid)

            },
            //获取详情数据
            initForm1(){

                this.$refs.ScienceEntrance1.initForm(this.sid)

            },
            handleClose1(done) {
                done();
            },
            handleClose(done) {
                        done();
            },
            goClouse(id,key){
                this.id = id;
                console.log(this.id);
                this.isClouse = true;
            },
            toClouse(){
                var value = this.value;
                var id = this.id;
                var that = this;
                if (value==''){
                    that.$message({
                        showClose: true,
                        type: 'warning',
                        message: '请选择结束原因'
                    });
                }
                that.$axios.post('/occupancy/closeById',this.$qs.stringify({
                    id:id,
                    closeReason:value,
                })).then(res=>{
                    console.log(res)
                    if (res.data.code==0){
                        that.$message({
                            showClose: true,
                            type: 'success',
                            message: res.data.msg
                        });
                        that.getInfo();
                        that.isClouse =false;
                    }
                })
            },
            search1(val,i){
                console.log(val)
                this.val = val;
                this.falg = false;

                var pageNum = i || 1;
                var that = this;
                that.$axios.get('/occupancy/showByPageForCenter',{
                    params:{
                        keyWord:val,
                        pageNum:pageNum,
                        pagesize:8,
                    }
                }).then((res)=>{
                    console.log(res)
                    if (res.data.code == 0){
                        that.info = res.data.data.list;
                        that.falg = false;
                        console.log(res.data.data.list);
                        that.total = res.data.data.total;
                    }
                })
            },
            handleSizeChange(val) {
                console.log(`每页 ${val} 条`);
            },
            handleCurrentChange(val) {
                console.log(`当前页: ${val}`);
                if (this.falg == true){
                    this.getInfo(val);
                }else {
                    this.search1(this.val,val)
                }

            },
            cancle(){
                this.isClouse = false;
            },
            //获取企业需求信息
            getInfo(i){
                var pageNum = i || 1;
                var that = this;
                var userName = sessionStorage.getItem('username');
                that.$axios.get('/occupancy/showByPageForCenter',{
                    params:{
                        pageNum:pageNum,
                        pagesize:8,
                    }
                }).then((res)=>{
                    console.log(res)
                    if (res.data.code == 0){
                        that.info = res.data.data.list;
                        console.log(res.data.data.list);
                        that.total = res.data.data.total;
                    }
                })
            }
        }
    }
</script>

<style scoped lang="less">
  .li {
    height: 45px;
    width: 100%;
    background-color: rgba(3, 87, 255, 0.0980392156862745);
    ul {
      display: flex;
      li {
        height: 100%;
        line-height: 45px;
        list-style: none;
        box-sizing: border-box;
        padding-left: 20px;
        font-size: 14px;
        color: #333;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;

      }
      li:nth-child(1){
        flex: 2;
      }
      li:nth-child(2){
        flex: 2;
      }
      li:nth-child(3){
        flex: 1;
      }
      li:nth-child(4){
        flex: 1;
      }
      li:nth-child(5){
        flex: 1;
      }
      li:nth-child(6){
        flex: 1.5;
        display: flex;
        span {
          display: inline-block;
          width: 30px;
          flex: 1;
          cursor: pointer;
        }
        span:nth-child(1){
          color: rgba(3, 87, 255, 0.749019607843137);
        }
        span:nth-child(2){
          color: rgba(3, 87, 255, 0.749019607843137);
        }
        span:nth-child(3){
          color: rgba(217, 0, 27, 0.749019607843137);
        }
      }
    }
  }
  .li-content {
    height: 72px!important;
  }
  .li-content ul li {
    line-height: 72px!important;
  }
  .clouse {
    width: 300px;
    height: 142px;
    position: absolute;
    left: 50%;
    top: 25%;
    background-color: #fff;
    border: 1px solid #333333;
    z-index: 999;
    transform: translate(-50%,-50%);
    box-sizing: border-box;
    padding: 32px 37px 15px 37px;
  }
  .clouse-select {
    display: inline-block;
    width: 138px;
    margin-left: 10px;
    margin-bottom: 10px;
  }
  .clouse-btn {
    text-align: center;
    padding-top: 15px;
  }
  .foot {
    position: absolute;
    right: 24px;
    bottom: 28px;
  }
</style>



