<template>
  <div>
    <!-- :rules="rules" -->
    <el-form v-if="childMsg1" :model="ruleForm" :rules="rules" ref="ruleForm" label-width="150px" class="demo-ruleForm">
    <el-form-item label="我的身份" prop="identification">
      <el-radio-group v-model="ruleForm.identification" disabled>
        <el-radio label="1">我是法人</el-radio>
        <el-radio label="2">我是代理人</el-radio>
      </el-radio-group>
    </el-form-item>

    <el-form-item label="统一社会信用代码" prop="societyCode">
      <div style="width: 186px">
        <el-input v-model="ruleForm.societyCode" disabled></el-input>
      </div>

    </el-form-item>

      <el-form-item label="法定代表人" prop="legalPerson">
        <div style="width: 186px">
        <el-input v-model="ruleForm.legalPerson" disabled></el-input>
        </div>
      </el-form-item>
      <el-form-item label="法人身份证号码" prop="persionCode">
        <div style="width: 186px">
          <el-input v-model="ruleForm.persionCode" disabled></el-input>
        </div>
      </el-form-item>
      <el-form-item label="附件" prop="name">
        <el-upload
          class="upload-demo"

          :action="global.baseURL+'/fileUpload'"
          :on-preview="handlePreview"
          :on-remove="handleRemove"
          :before-remove="beforeRemove"
          :on-success="handleAvatarSuccess"
          multiple
          :limit="3"
          :on-exceed="handleExceed"
          :file-list="fileList">
          <el-button size="small" type="primary" disabled>点击上传</el-button>
          <div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过500kb</div>
        </el-upload>
      </el-form-item>
      <el-form-item label="手机号码" prop="phoneNumber">
        <div style="width: 186px">
        <el-input v-model="ruleForm.phoneNumber" disabled></el-input>
        </div>
      </el-form-item>
      <!-- <el-form-item label="验证码" prop="name">
        <div style="width: 102px">
          <el-input v-model="ruleForm.name"></el-input>
        </div>
      </el-form-item> -->
      <!-- <el-form-item label="手机验证码" prop="name">
        <div style="width: 102px">
          <el-input v-model="ruleForm.name"></el-input>
        </div>
      </el-form-item> -->
      <el-form-item v-if="ruleForm.identification!=1" label="代理人姓名" prop="agentPerson">
        <div style="width: 186px">
        <el-input v-model="ruleForm.agentPerson" disabled></el-input>
        </div>
      </el-form-item>
      <el-form-item v-if="ruleForm.identification!=1" label="身份证号码" prop="apPersionCode">
        <div style="width: 186px">
        <el-input v-model="ruleForm.apPersionCode" disabled></el-input>
          </div>
      </el-form-item>
      <div style="display:flex;justify-content:center">
        <el-button v-if="!childMsg1" :type="isReset?'danger':'primary'" @click="submitForm">{{isReset?"重新认证":"认证"}}</el-button>
        <!-- <el-button type="danger">重新认证</el-button> -->
        <el-button v-if="!childMsg1" @click="close">关闭</el-button>
      </div>
  </el-form>

    <el-form v-if="!childMsg1" :model="ruleForm" :rules="rules" ref="ruleForm" label-width="150px" class="demo-ruleForm">
      <el-form-item label="我的身份" prop="identification">
        <el-radio-group v-model="ruleForm.identification">
          <el-radio label="1">我是法人</el-radio>
          <el-radio label="2">我是代理人</el-radio>
        </el-radio-group>
      </el-form-item>

      <el-form-item label="统一社会信用代码" prop="societyCode">
        <div style="width: 186px">
          <el-input v-model="ruleForm.societyCode"></el-input>
        </div>

      </el-form-item>

      <el-form-item label="法定代表人" prop="legalPerson">
        <div style="width: 186px">
          <el-input v-model="ruleForm.legalPerson"></el-input>
        </div>
      </el-form-item>
      <el-form-item label="法人身份证号码" prop="persionCode">
        <div style="width: 186px">
          <el-input v-model="ruleForm.persionCode"></el-input>
        </div>
      </el-form-item>
      <el-form-item label="附件" prop="name">
        <el-upload
          class="upload-demo"

          :action="global.baseURL+'/fileUpload'"
          :on-preview="handlePreview"
          :on-remove="handleRemove"
          :before-remove="beforeRemove"
          :on-success="handleAvatarSuccess"
          multiple
          :limit="3"
          :on-exceed="handleExceed"
          :file-list="fileList">
          <el-button size="small" type="primary">点击上传</el-button>
          <div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过500kb</div>
        </el-upload>
      </el-form-item>
      <el-form-item label="手机号码" prop="phoneNumber">
        <div style="width: 186px">
          <el-input v-model="ruleForm.phoneNumber"></el-input>
        </div>
      </el-form-item>
      <!-- <el-form-item label="验证码" prop="name">
        <div style="width: 102px">
          <el-input v-model="ruleForm.name"></el-input>
        </div>
      </el-form-item> -->
      <!-- <el-form-item label="手机验证码" prop="name">
        <div style="width: 102px">
          <el-input v-model="ruleForm.name"></el-input>
        </div>
      </el-form-item> -->
      <el-form-item v-if="ruleForm.identification!=1" label="代理人姓名" prop="agentPerson">
        <div style="width: 186px">
          <el-input v-model="ruleForm.agentPerson"></el-input>
        </div>
      </el-form-item>
      <el-form-item v-if="ruleForm.identification!=1" label="身份证号码" prop="apPersionCode">
        <div style="width: 186px">
          <el-input v-model="ruleForm.apPersionCode"></el-input>
        </div>
      </el-form-item>
      <div style="display:flex;justify-content:center">
        <el-button v-if="!childMsg1" :type="isReset?'danger':'primary'" @click="submitForm">{{isReset?"重新认证":"认证"}}</el-button>
        <!-- <el-button type="danger">重新认证</el-button> -->
        <el-button v-if="!childMsg1" @click="close">关闭</el-button>
      </div>
    </el-form>
  </div>
</template>

<script>
    import axios from 'axios'
    export default {
        name: "CompanyForm",
        props:['baseFormInfoData','childMsg1',],
        data(){
            return {
                isReset:false,//是否显示重新认证
                fileList: [],
                fileLists: [],
                ruleForm: {
                    // name: '',
                    // region: '',
                    // date1: '',
                    // date2: '',
                    // delivery: false,
                    identification:'',//身份识别
                    societyCode:'',//信用代码
                    legalPerson:'',//法人
                    persionCode:'',//法人身份证号
                    address:'',//附件地址
                    phoneNumber: '',//手机号码
                    agentPerson: '',//代理人姓名
                    apPersionCode: '',//代理人身份证号
                },
                rules:  {
                    legalPerson: [
                      { required: true, message: '请输入法定代表人名称', trigger: 'blur' },
                      { min: 1, max: 100, message: '长度在 1 到 12 个字符', trigger: 'blur' }
                    ],
                    agentPerson: [
                      { required: true, message: '请输入代理人名称', trigger: 'blur' },
                      { min: 1, max: 100, message: '长度在 1 到 12 个字符', trigger: 'blur' }
                    ],
                    societyCode: [
                      { required: true, message: '请输入社会信用代码', trigger: 'blur' },

                      { pattern: /^[^_IOZSVa-z\W]{2}\d{6}[^_IOZSVa-z\W]{10}$/g, message: '请输入正确的社会信用代码', trigger: 'blur' },
                    ],
                    persionCode: [
                      { required: true, message: '请输入身份证号码', trigger: 'blur' },
                      { pattern: /^(([1][1-5])|([2][1-3])|([3][1-7])|([4][1-6])|([5][0-4])|([6][1-5])|([7][1])|([8][1-2]))\d{4}(([1][9]\d{2})|([2]\d{3}))(([0][1-9])|([1][0-2]))(([0][1-9])|([1-2][0-9])|([3][0-1]))\d{3}[0-9xX]$/, message: '请输入正确的身份证号码', trigger: 'blur' },
                   ],
                   apPersionCode: [
                      { required: true, message: '请输入身份证号码', trigger: 'blur' },
                      { pattern: /^(([1][1-5])|([2][1-3])|([3][1-7])|([4][1-6])|([5][0-4])|([6][1-5])|([7][1])|([8][1-2]))\d{4}(([1][9]\d{2})|([2]\d{3}))(([0][1-9])|([1][0-2]))(([0][1-9])|([1-2][0-9])|([3][0-1]))\d{3}[0-9xX]$/, message: '请输入正确的身份证号码', trigger: 'blur' },
                   ],
                   phoneNumber: [
                      { required: true, message: '请输入手机号码', trigger: 'blur' },
                      { pattern: /^1[3456789]\d{9}$/, message: '请输入正确的手机号码', trigger: 'blur' },
                   ],
                    identification: [
                      { required: true, message: '请选择我的身份', trigger: 'change' }
                    ],
                },
            }
        },
        created(){

            var status = sessionStorage.getItem('status');
            if (status==2 || this.childMsg1){
                this.getInfo()
            }
        },
        methods:{
            getInfo(){
                var that = this;
                var id = that.$store.state.companyId;
                that.$axios.get('bussinessCenter/findBussinessCenter',{
                    params:{
                        id:id
                    }
                })
                    .then(res=>{
                        console.log(res)
                        if (res.data.code==0){
                            console.log(res.data.data)
                            that.ruleForm.persionCode   = res.data.data.persionCode;
                            that.ruleForm.legalPerson   = res.data.data.legalPerson;
                            that.ruleForm.societyCode   = res.data.data.societyCode;
                            that.ruleForm.identification   = res.data.data.identification;
                            that.ruleForm.phoneNumber   = res.data.data.phoneNumber;
                            that.ruleForm.agentPerson   = res.data.data.agentPerson;
                            that.ruleForm.apPersionCode   = res.data.data.apPersionCode;
                            var addressName = res.data.data.address.split('/')
                            console.log(addressName[addressName.length-1])

                            that.fileList.push(   {name: addressName[addressName.length-1], url: res.data.data.address})

                        }
                    })
            },
            submitForm() {
                this.$refs["ruleForm"].validate((valid) => {
                    if (valid) {
                      this.authentication();
                        // alert('submit!');
                    } else {
                        console.log('error submit!!');
                        return false;
                    }
                });
            },
            //读取认证信息
            getVerifyInfo(){
                var that = this;
                var userName = sessionStorage.getItem('username');
                this.$axios.get('bussinessCenter/findMyBusinessCenter',{
                    params:{
                        userName:userName
                    }
                }).then(res=>{
                    console.log(res)
                    if (res.data.code==0){
                        if (!res.data.data){
                            that.status = 'null';
                            sessionStorage.setItem('status','');
                        }else {
                            sessionStorage.setItem('status',res.data.data.statue);
                            that.$store.state.companyId = res.data.data.id
                        }
                    }else{

                    }
                    console.log(res)
                })
            },
            //认证事件
            authentication(){
              // console.log(this.fileLists);
              let param={};
              var that = this;
              console.log('baseFormInfoData')
                console.log(this.baseFormInfoData)
              for(let key in this.baseFormInfoData){
                param[key]=this.baseFormInfoData[key]
              }

              console.log('param')
                param.recordDate = param.recordDate.substring(0,10)
                console.log(param)
                if (param.otherTypeCom){
                    param.typeEnterprise = param.otherTypeCom
                }

              this.ruleForm.address=this.fileLists.length>0?this.fileLists.join(","):'';

              for(let key in this.ruleForm){
                param[key]=this.ruleForm[key]
              }
               var  username = sessionStorage.getItem('username');
              param.uName= username;
              // console.log(param);
                var  status =  sessionStorage.getItem('status');
                var id = this.$store.state.companyId;
                if (status==2){
                    param.id = id
                    axios.get('bussinessCenter/updateBusinessCenter',{
                        params:param
                    }).then((res)=>{
                        // console.log(res);
                        if(res.data.code==0){
                            this.$message({
                                showClose: true,
                                message: '提交成功',
                                type: 'success'
                            });
                            this.$parent.getVerifyInfo();
                            this.$parent.modifyStatus();
                            this.$parent.toAuthentication(2);
                        }else{
                            this.$message({
                                showClose: true,
                                message: res.data.msg,
                                type: 'error'
                            });
                            this.isReset=true;
                        }
                        // console.log(res);
                    })
                }else {
                    axios.get('bussinessCenter/addAuthentication',{
                        params:param
                    }).then((res)=>{
                        // console.log(res);
                        if(res.data.code==0){
                            this.$message({
                                showClose: true,
                                message: '提交成功',
                                type: 'success'
                            });
                            this.$parent.toAuthentication(2);
                            this.$parent.getVerifyInfo();
                        }else{
                            this.$message({
                                showClose: true,
                                message: res.data.msg,
                                type: 'error'
                            });
                            this.isReset=true;
                        }
                        // console.log(res);
                    })
                }

            },
            resetForm(formName) {
                this.$refs[formName].resetFields();
            },
            //图片
            handleRemove(file, fileList) {
                console.log(file, fileList);
                this.fileLists=[];
                for(let i=0;i<fileList.length;i++){
                  this.fileLists.push(fileList[i].response.data);
                }
                // this.fileList=
            },
            handlePreview(file) {
                console.log(file);
            },
            handleExceed(files, fileList) {
                this.$message.warning(`当前限制选择 3 个文件，本次选择了 ${files.length} 个文件，共选择了 ${files.length + fileList.length} 个文件`);
            },
            beforeRemove(file, fileList) {
                return this.$confirm(`确定移除 ${ file.name }？`);
            },
            handleAvatarSuccess(res, file) {
              // console.log(res.data);
              if(res.code==0){
                this.fileLists.push(res.data);
              }else{
                this.$message({
                    showClose: true,
                    message: '附件上传失败',
                    type: 'error'
                });
                // console.log('error',this.fileLists);
                if(this.fileList){
                  this.fileList.pop();
                }

              }

              // console.log(this.fileLists);
              // this.imageUrl = URL.createObjectURL(file.raw);
            },
            //关闭
            close(){
                this.$parent.toAuthentication(2);
                this.$parent.getVerifyInfo();
            }
        }
    }
</script>

<style scoped lang="less">

</style>



