            }
        },
        props:['childMsg'],
        created(){
            console.log(this.childMsg)
            var status = sessionStorage.getItem('status');
            if (status==2||this.childMsg){
                this.getInfo()
            }
        },
        mounted () {
          console.log(baseURL.baseURL);
        },
        computed:{
            companyInfo:function () {
                return this.$store.state.companyInfo
            }
        },
        // mixins:{baseURL,baseURL},
        methods:{
            getInfo(){
                var that = this;
                var id = that.$store.state.companyId;
                that.$axios.get('bussinessCenter/findBussinessCenter',{
                    params:{
                        id:id
                    }
                })
                    .then(res=>{
                        console.log(res)
                        if (res.data.code==0){
                            console.log(res.data.data)
                            that.form.firmName   = res.data.data.firmName;
                            that.form.enterpriseProfile   = res.data.data.enterpriseProfile
                            that.form.registeredCapital   = res.data.data.registeredCapital
                            that.form.scale = res.data.data.scale;
                            that.form.typeEnterprise = res.data.data.typeEnterprise
                            that.form.recordDate = res.data.data.recordDate
                            console.log(res.data.data.industry.split(';'))
                            that.form.industry = res.data.data.industry.split(';');
                             that.typeEnterprise1 = res.data.data.typeEnterprise;
                             that.selectedOptions.push(res.data.data.country)
                            that.selectedOptions.push(res.data.data.city)
                            that.selectedOptions.push(res.data.data.district)
                            var logoName = res.data.data.logo.split('/')
                            console.log(logoName[logoName.length-1])
                            // fileList: []
                            that.form.fileList.push(   {name: logoName[logoName.length-1], url: res.data.data.logo})
                            console.log(that.selectedOptions)
                            // {name:'logo.jpeg',url:res.data.data.logo}
                        }
                    })
            },
            nextTips() {
                console.log('submit!');
                console.log(this.form);
                this.$refs["form"].validate((valid) => {
                  if (valid) {
                    // alert('submit!');
                    this.form.industry=this.form.industry.join(";");
                    console.log(this.form.industry);
                    this.$emit('baseFormUp',this.form);
                    this.$parent.totab(2);
                  } else {
                    console.log('error submit!!');
                    return false;
                  }
                });

            },
            handleChange (value) {
                console.log(value)
                var arr = [];
                value.forEach((item,index)=>{
                    arr.push(CodeToText[item])
                })
                this.form.country=arr[0];
                this.form.city = arr[1];
                this.form.district = arr[2];
                console.log(this.form.city)
            },
            handleChange1(){

            },
            close(){
                this.$parent.toAuthentication(2);
            },
            handleRemove(file, fileList) {
              this.form.logo='';
              console.log(file, fileList);
            },
            handlePreview(file) {
              console.log(file);
            },
            handleExceed(files, fileList) {
              this.$message.warning(`当前限制选择 1 个文件，本次选择了 ${files.length} 个文件，共选择了 ${files.length + fileList.length} 个文件`);
            },
            handleAvatarSuccess(res, file) {
              console.log(res);
              if(res.code==0){
                this.form.logo=res.data;
              }else{
                this.$message({
                    showClose: true,
                    message: 'logo上传失败',
                    type: 'error'
                });
                this.form.fileList=[];
              }
              console.log(this.form.fileList);
            },
        }
    }
</script>

