<template>
  <div style="position: relative;height: 100%">
    <div class="li">
      <ul>
        <li>申报项目类型类型</li>
        <li>项目名称</li>
        <li>申报状态</li>
        <li>申报时间</li>
        <li>操作列</li>
      </ul>
      <div class="list_data">
        <ul class="li-content" v-for="item in industrialData" :key="item.id">
          <li>{{item.declarationType}}</li>
          <li>{{item.projectName}}</li>
          <li>{{item.approvalStatus==0?'待审核':item.approvalStatus==1?'已审核':'驳回'}}</li>
          <li>{{item.createTime}}</li>
          <li><span @click="openDetail(item.id,item.declarationType)">详情</span><span style="color: rgba(217, 0, 27, 0.749019607843137)"  @click="delDetail(item.id)">删除</span> <span v-if="item.approvalStatus==2" style="color: rgba(3, 87, 255, 0.74901961);cursor: pointer">修改</span><span v-if="item.approvalStatus!=2" style="color: #ccc;cursor: default">修改</span></li>
          <!-- @click="isClouse = true" -->
        </ul>
      </div>
    </div>
    <div class="foot">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="pageNum"
        :page-sizes="[10, 20, 30, 40]"
        :page-size="pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="listTotal">
      </el-pagination>
    </div>
    <!-- 详情弹框 -->
    <el-dialog  :visible.sync="dialogFormVisible">
      <component :is="currentDialog" :formData="formData" ></component>
    </el-dialog>
  </div>
</template>

<script>
    import {getindustrialdeclarationData,getindustrialdeclarationDetail,delindustrialdeclarationDetail} from '@/request/urlConfig'
    import Form1 from "../Industrial/IndusForm/form1"
    import Form2 from "../Industrial/IndusForm/form2"
    import Form3 from "../Industrial/IndusForm/form3"
    import Form4 from "../Industrial/IndusForm/form4"
    import Form5 from "../Industrial/IndusForm/form5"
    // import Form6 from "../Industrial/IndusForm/form6"
    import Form7 from "../Industrial/IndusForm/form7"
    import Form8 from "../Industrial/IndusForm/form8"
    import Form9 from "../Industrial/IndusForm/form9"
    export default {
        name: "IndustrialApplication",
        data(){
            return {
                industrialData:[],
                pageSize:10,
                pageNum:1,
                keyWord:'',
                listTotal:0,
                dialogFormVisible:false,
                currentDialog:'Form1',
                formData:{},//form弹框数据
                options: [{
                    value: '选项1',
                    label: '需求对接解决'
                }, {
                    value: '选项2',
                    label: '企业内部解决'
                }, {
                    value: '选项3',
                    label: '其余资源解决'
                }, {
                    value: '选项4',
                    label: '填写错误'
                }],
                value: '',
            }
        },
        components: {
          "Form1":Form1,
          "Form2":Form2,
          "Form3":Form3,
          "Form4":Form4,
          "Form5":Form5,
          "Form7":Form7,
          "Form8":Form8,
          "Form9":Form9,
        },
        methods:{
            //获取列表数据
            getListData(){
              let param={
                keyWord:this.keyWord,
                pageSize:this.pageSize,
                pageNum:this.pageNum
              }
              getindustrialdeclarationData(param).then(res=>{
                if(res.data.code==0){
                  this.industrialData=res.data.data.list;

                  this.listTotal=res.data.data.total;
                }else{
                  this.industrialData=[];
                  this.listTotal=0;
                  this.$message({
                    showClose: true,
                    message: '返回数据有误',
                    type: 'error'
                  });
                }
              })
            },
            //列表页分页功能
            handleSizeChange(val) {
              this.pageSize=val;
              this.getListData();
              // console.log(`每页 ${val} 条`);
            },
            handleCurrentChange(val) {
              this.pageNum=val;
              this.getListData();
              // console.log(`当前页: ${val}`);
            },
            //keyword查询功能
            searchIndustrial(data){
              this.keyWord=data;
              this.pageNum=1;
              this.getListData();
            },
            //打开详情
            openDetail(data,name){
                console.log(name);
                var name = name;
                let param={
                id:data
              }
              getindustrialdeclarationDetail(param).then(res=>{
                console.log(res);
                this.formData=res.data.data;
                  switch (name) {
                      case "工业转型升级技术改造项目":
                          this.currentDialog = 'Form1'
                          console.log(this.currentDialog)
                          break;
                      case "企业技术创新项目":
                          this.currentDialog = 'Form2'
                          console.log(this.currentDialog)
                          break;
                      case "绿色制造项目":
                          this.currentDialog = 'Form3'
                          console.log(this.currentDialog)
                          break;
                      case "民营经济提升":
                          this.currentDialog = 'Form4'
                          console.log(this.currentDialog)
                          break;
                      case "装备工业项目":
                          this.$store.state.IndusTitle = '装备工业项目项目申请表';
                          this.currentDialog = 'Form5'
                          console.log(this.currentDialog)
                          break;
                      case "电子信息产业（电子信息制造业，软件业）":
                          this.$store.state.IndusTitle = '电子信息产业（电子信息制造业、软件业）项目申报表';
                          this.currentDialog = 'Form5'
                          console.log(this.currentDialog)
                          break;
                      case "集成电路产业项目":
                          this.$store.state.IndusTitle = '集成电路产业项目申报表';
                          this.currentDialog = 'Form5'
                          console.log(this.currentDialog)
                          break;
                      case "智能语音及人工智能产业发展项目":
                          this.$store.state.IndusTitle = '智能语音及人工智能产业发展项目申请表';
                          this.currentDialog = 'Form7'
                          console.log(this.currentDialog)
                          break;
                      case "互联网与制造业融合项目":
                          this.$store.state.IndusTitle = '互联网与制造业融合项目申请表';
                          this.currentDialog = 'Form8'
                          console.log(this.currentDialog)
                          break;
                      case "非煤矿山建设项目":
                          this.$store.state.IndusTitle = '非煤矿山建设项目申请表';
                          this.currentDialog = 'Form9'
                          console.log(this.currentDialog)
                          break;
                      case "传统工艺美术保护发展项目":
                          this.$store.state.IndusTitle = '传统工艺美术保护发展项目申请表';
                          this.currentDialog = 'Form7'
                          console.log(this.currentDialog)
                          break;
                  }
                this.dialogFormVisible=true;
              })

            },
            delDetail(id){
              let data={
                id
              }

              this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
              }).then(() => {
                delindustrialdeclarationDetail(data).then(res=>{

                  console.log(res);
                  this.$message({
                    type: 'success',
                    message: res.data.data
                  });
                  this.getListData();
                })
              }).catch((error) => {
                console.log(error);
                this.$message({
                  type: 'info',
                  message: '已取消删除'
                });
              });
            },
            cancle(){
                this.isClouse = false;
            },
            toClouse(){
                this.isClouse = false;
            }
        },
        created () {
          this.getListData();
        },
    }
</script>

<style scoped lang="less">
  .li {
    height: 45px;
    width: 100%;
    background-color: rgba(3, 87, 255, 0.0980392156862745);
    ul {
      display: flex;
      li {
        height: 100%;
        line-height: 45px;
        list-style: none;
        box-sizing: border-box;
        padding-left: 20px;
        font-size: 14px;
        color: #333;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
      }
      li:nth-child(1){
        flex: 1.5;
      }
      li:nth-child(2){
        flex: 2;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
      }
      li:nth-child(3){
        flex: 1;
      }
      li:nth-child(4){
        flex: 1;
      }
      li:nth-child(5){
        flex: 1.5;
        display: flex;
        span {
          display: inline-block;
          width: 30px;
          flex: 1;
          cursor: pointer;
        }
        span:nth-child(1){
          color: rgba(3, 87, 255, 0.749019607843137);
        }
        span:nth-child(2){
          color: rgba(3, 87, 255, 0.749019607843137);
        }
        span:nth-child(3){
          color: rgba(217, 0, 27, 0.749019607843137);
        }
      }
    }
  }
  .list_data{
    height:70vh;
    overflow: auto;
  }
  .li-content {
    height: 72px!important;
  }
  .li-content ul li {
    line-height: 72px!important;
  }
  .clouse {
    width: 300px;
    height: 142px;
    position: absolute;
    left: 50%;
    top: 50%;
    background-color: #fff;
    border: 1px solid #333333;
    z-index: 999;
    transform: translate(-50%,50%);
    box-sizing: border-box;
    padding: 32px 37px 15px 37px;
  }
  .clouse-select {
    display: inline-block;
    width: 138px;
    margin-left: 10px;
    margin-bottom: 10px;
  }
  .clouse-btn {
    text-align: center;
    padding-top: 15px;
  }
  .foot {
    position: absolute;
    right: 24px;
    bottom: 28px;
  }
</style>



