<template>
    <div>
      <div class="head">
        <div class="head-wrap">
          <div class="welcome">
            <span>{{day}}，</span>
            <span>欢迎来到政企智慧服务平台</span>
          </div>
          <div class="login" >
            <span @click="$router.push('/login')" style="cursor: pointer" v-if="!username">登陆</span>
          </div>
          <div class="login1">
            <span  style="cursor: pointer" v-if="username">欢迎您！{{username}}</span>
            <span v-if="username"  style="display: inline-block;margin-left: 10px;font-size: 14px;cursor: pointer;color: #1e58a3">企业中心</span>
            <span v-if="username" style="display: inline-block;margin-left: 10px;font-size: 14px;cursor: pointer;color: #1e58a3" @click="goBack">退出</span>
          </div>
          <div class="register">
            <span @click="$router.push('/register')" style="cursor: pointer" v-if="!username">注册</span>
          </div>
          <div class="wx" @mouseenter="isCode=true"
               @mouseleave="isCode=false"
          >
            <span  style="cursor: pointer">微信公众号</span>
            <img src="../../assets/code.jpg" alt="" style="width: 160px;position: absolute;" class="code" v-show="isCode">

          </div>
        </div>

      </div>
      <div class="search">
        <div class="search-text">
          <span style="cursor: pointer" @click="$router.push('/Introduce/home')">政企智慧服务平台</span>
          <span v-if="$route.path.indexOf('iHome/IndusIndex') >= 0">>工业项目投资计划导向服务系统</span>
        </div>
        <div class="search-input">
          <input type="text" placeholder="请输入关键词"/>
          <span>搜索</span>
        </div>
      </div>
      <div class="position">
        <div class="position-warp">
          <span></span>  <span>位置:政企智慧服务平台</span> &nbsp;<span> > </span>&nbsp; <span>企业中心</span>
        </div>
      </div>
      <div class="center-header">
         <div class="center-body">
              <div class="center-list">
                 <ul>
                   <li><span style="color: #383838;font-size: 23px;">企业中心</span><span style="font-size: 12px;color: #bababa;" >/  Business Center</span></li>
                   <li :class="current=='EnterpriseCertification'?'active':''" @click="toTab(1)">企业认证</li>
                   <li :class="current=='TalentDemand'?'active':''" @click="toTab(2)" >人才需求</li>
                   <li :class="current=='ScientificAchievements'?'active':''" @click="toTab(5)" >科技成果</li>
                   <li :class="current=='IndustrialApplication'?'active':''" @click="toTab(3)" >工业项目申报</li>
                   <li :class="current=='IndustrialFinancing'?'active':''" @click="toTab(4)" >工业项目融资</li>
                   <li :class="current=='EnterpriseNeeds'?'active':''" @click="toTab(6)" >企业需求</li>
                   <li :class="current=='MessageNotification'?'active':''" @click="toTab(7)" >消息通知</li>
                 </ul>
              </div>
              <div class="center-info">
                  <div class="info-head">
                    <span>{{tabsName}}</span>
                    <div class="keyWords" v-show="keyword">
                      <span style="font-size: 14px;" >关键词</span>
                      <div class="keyWords-input">
                        <el-input v-model="input" placeholder="请输入内容"></el-input>
                      </div>
                      <el-button size="small" @click="search">查询</el-button>
                      <el-button type="primary" size="small" v-show="keyButton" @click="toRelease">立即发布</el-button>
                      <el-button type="primary" size="small" v-show="keyButton1" @click="toSettle">立即入驻</el-button>
                      <el-button type="primary" size="small" v-show="keyButton2" @click="toDeclare">立即申报</el-button>
                    </div>
                  </div>
                  <div class="center-component" style="overflow:auto">
                    <keep-alive></keep-alive>
                    <component :is="current" ref="child"></component>
                  </div>
