<script>
  import EnterpriseCertification from "./EnterpriseCertification";
  import EnterpriseNeeds from "./EnterpriseNeeds";
  import IndustrialApplication from "./IndustrialApplication";
  import IndustrialFinancing from "./IndustrialFinancing";
  import MessageNotification from "./MessageNotification";
  import ScientificAchievements from "./ScientificAchievements";
  import TalentDemand from "./TalentDemand";
  import foot from "../foot";
    export default {
        name: "EnterpriseCenter",
        data(){
            return {
                current:'EnterpriseCertification',
                tabsName:'企业认证',
                input:'',
                keyButton:false,
                keyButton1:false,
                keyButton2:false,
                keyword:true,
                username:'',
                isCode:false,
                day:''
            }
        },
        created(){
            this.username = sessionStorage.getItem('username');
            console.log(this.username);
            this.current = 'EnterpriseCertification';
            this.tabsName = '企业认证';
            this.keyword = false;
            this.getTime();
        },
        components:{
            EnterpriseCertification,
            EnterpriseNeeds,
            IndustrialApplication,
            IndustrialFinancing,
            MessageNotification,
            ScientificAchievements,
            TalentDemand,
            foot
        },
        methods:{
            //获取最新时间
            getTime(){
                var myDate = new Date();
                var year = myDate.getFullYear();
                var month = myDate.getMonth() + 1;
                var day  =  myDate.getDate();
                console.log(year+'-' + month + '-' +day)
                this.day = year+'-' + month + '-' +day;

            },
            toDeclare(){
                if (this.current == 'IndustrialApplication'){
                    this.$router.push('/iHome/IndusForm')
                }
            },
            toFinace(){
                this.$alert('功能开发中','提醒', {
                    confirmButtonText: '确定',
                });
            },
            //退出
            goBack(){
              var that = this;
                this.$confirm('此操作退出账户, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.$message({
                        type: 'success',
                        message: '退出登陆!'
                    });
                    that.$axios.get('user/logout')
                        .then(res=>{
                            console.log(res)
                        })
                    sessionStorage.removeItem('username');
                    that.$router.push('/')
                    location.reload()
                    that.$router.go(0)
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消'
                    });
                });
            },
            toSettle(){
             if (this.current=='ScientificAchievements'){
                 this.$router.push('/sHome/ScienceEntrance')
             }
            },
            //发布
            toRelease(){
                //企业需求
                var that = this;
                var status = sessionStorage.getItem('status');
                if ( this.current == 'EnterpriseNeeds'){
                         this.$router.push('/Home/NeedRelease')
                }

            },
            //检索
            search(){
                var params = this.current;
                var val = this.input;
                // console.log(params)
                //企业需求
                if (params == 'EnterpriseNeeds'){
                    this.$refs.child.search(val,1)
                }else if (params == 'ScientificAchievements'){
                    this.$refs.child.search1(val,1)
                }else if (params == 'IndustrialApplication'){
                    // console.log(this.$refs.child);
                    this.$refs.child.searchIndustrial(val)
                }

            },
            //组件跳转
            toTab(i){
                if (i==1){
                    this.current = 'EnterpriseCertification';
                    this.tabsName = '企业认证';
                    this.keyword = false;
                }
                if (i==2){
                    this.$alert('功能开发中','提醒', {
                        confirmButtonText: '确定',
                    });
                    // this.tabsName = '人才需求';
                    // this.keyword = true;
                    // this.keyButton = true;
                    // this.keyButton1 =false;
                    // this.keyButton2 = false;
                }
                if (i==3){
                    this.current = 'IndustrialApplication';
                    this.tabsName = '工业项目申报';
                    this.keyword = true;
                    this.keyButton = false;
                    this.keyButton1 =false;
                    this.keyButton2 = true;
                }
                if (i==4){
                    this.$alert('功能开发中','提醒', {
                        confirmButtonText: '确定',
                    });
                    // this.current = 'IndustrialFinancing';
                    // this.tabsName = '工业项目融资';
                    // this.tabsName = '工业项目申报';
                    // this.keyword = true;
                    // this.keyButton = false;
                    // this.keyButton1 =false;
                    // this.keyButton2 = true;
                }
                if (i==5){
                    this.current = 'ScientificAchievements';
                    this.tabsName = '科技成果';
                    this.keyword = true;
                    this.keyButton = false;
                    this.keyButton1 = true;
                    this.keyButton2 = false;
                }
                if (i==6){
                    this.current = 'EnterpriseNeeds';
                    this.tabsName = '企业需求';
                    this.keyButton = true;
                    this.keyword = true;
                    this.keyButton = true;
                    this.keyButton1 = false;
                    this.keyButton2 = false;
                }
                if (i==7){
                    this.current = 'MessageNotification';
                    this.tabsName = '消息通知';
                    this.keyword = false;
                }
            },
        },
    }
</script>

